import { defineComponent, onMounted, onUnmounted, reactive, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';
export default defineComponent({
  name: 'ContractResult',

  setup() {
    let timer = ref(0);
    const store = useStore();
    const route = useRoute();
    const rotuer = useRouter();
    let params = reactive({
      creditUserId: '',
      name: '',
      idno: '',
      tele: ''
    });
    onMounted(() => {
      timer.value = setInterval(() => {
        console.log('执行调用');
        reload();
      }, 3000);
    });
    onUnmounted(() => {
      console.log('被销毁');
      clearInterval(timer.value);
    });

    const reload = () => {
      const {
        params: {
          name = '',
          idno = '',
          tele = ''
        }
      } = route;
      let urlParams = {
        data: {
          name,
          idno,
          tele
        }
      };
      store.dispatch('interModule/fetchSignStats', urlParams).then(res => {
        if (res.signStatus === '2') {
          let urlParams = {
            data: {
              creditUserId: params.creditUserId,
              name: params.name,
              idno: params.idno
            }
          };
          store.dispatch('interModule/fetchContractInfo', urlParams).then(() => {
            rotuer.push(`/perinfo/${params.creditUserId}/${params.name}/${params.idno}`);
          });
        }
      });
    };
  }

});